import React, { useMemo } from 'react';
import { useLocation } from 'react-router-dom';
import ArrowR from '@assets/icons/arrowR.svg';
import { Navigation, LinkText, List, NavUl, Content, EndCrumb } from './style';
import { ROUTES } from '../../../routes/constants';

const Breadcrumbs = () => {
    const location = useLocation();
    const breadcrumbPaths = useMemo(() => {
        const paths = location.pathname.split('/').filter((path) => path !== '');
        return paths.map((path, index) => ({
            path: paths.slice(0, index + 1).join('/'),
            title: getBreadcrumbTitle(path),
        }));
    }, [location]);

    return (
        <Navigation>
            <NavUl>
                {breadcrumbPaths.map((breadcrumb, index) => {
                    const isLast = index === breadcrumbPaths.length - 1;

                    return (
                        <List key={breadcrumb.path}>
                            {!isLast ? (
                                <Content>
                                    <LinkText to={`/${breadcrumb.path}`}>{breadcrumb.title}</LinkText> <ArrowR />
                                </Content>
                            ) : (
                                <EndCrumb>{breadcrumb.title}</EndCrumb>
                            )}
                        </List>
                    );
                })}
            </NavUl>
        </Navigation>
    );
};

const getBreadcrumbTitle = (path: string) => {
    switch (path) {
        case ROUTES.YANKI:
            return '–ì–ª–∞–≤–Ω–∞—è';
        case ROUTES.CATALOG:
            return '–ö–∞—Ç–∞–ª–æ–≥';
        case ROUTES.CATEGORY:
            return '–ö–∞—Ç–µ–≥–æ—Ä–∏–∏';
        case ROUTES.FAVOURITES:
            return '–ò–∑–±—Ä–∞–Ω–Ω–æ–µ';
        case ROUTES.PAYMENT:
            return '–û–ø–ª–∞—Ç–∞ –∏ –¥–æ—Å—Ç–∞–≤–∫–∞';
        case ROUTES.ABOUTS:
            return '–ö–æ–Ω—Ç–∞–∫—Ç—ã';
        case ROUTES.CART:
            return '–ö–æ—Ä–∑–∏–Ω–∞';
        case ROUTES.REFUND:
            return '–û–±–º–µ–Ω –∏ –≤–æ–≤—Ä–∞—Ç';
        case ROUTES.PROFILE:
            return '–õ–∏—á–Ω—ã–π –∫–∞–±–∏–Ω–µ—Ç';
        default:
            return path.charAt(0).toUpperCase() + path.slice(1);
    }
};
export default Breadcrumbs;
