import * as yup from 'yup';

export enum ADD_ORDER_PROFILE {
    NAME = 'name',
    SURNAME = 'surname',
    EMAIL = 'email',
    PHONE = 'phone',
    CITY = 'city',
    POSTOFFICE = 'postOffice',
    DELIVERY = 'delivery',
    PAYMENT = 'payment',
}
const phoneRegExp = /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/;
export const orderRegistration = yup.object().shape({
    [ADD_ORDER_PROFILE.NAME]: yup.string().required('–£–∫–∞–∂–∏—Ç–µ –∏–º—è'),
    [ADD_ORDER_PROFILE.SURNAME]: yup.string().required('–£–∫–∞–∂–∏—Ç–µ —Ñ–∞–º–∏–ª–∏—é'),
    [ADD_ORDER_PROFILE.EMAIL]: yup.string().required('–£–∫–∞–∂–∏—Ç–µ –ø–æ—á—Ç—É').email('–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω–∞—è —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω–∞—è –ø–æ—á—Ç–∞'),
    [ADD_ORDER_PROFILE.PHONE]: yup
        .string()
        .required('–£–∫–∞–∂–∏—Ç–µ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞')
        .matches(phoneRegExp, '–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ —É–∫–∞–∑–∞–Ω –Ω–µ –ø—Ä–∞–≤–∏–ª—å–Ω–æ'),
    [ADD_ORDER_PROFILE.CITY]: yup.string().required('–£–∫–∞–∂–∏—Ç–µ –≥–æ—Ä–æ–¥'),
    [ADD_ORDER_PROFILE.POSTOFFICE]: yup.string().required('–£–∫–∞–∂–∏—Ç–µ –æ—Ç–¥–µ–ª–µ–Ω–∏–µ –ø–æ—á—Ç—ã'),
    [ADD_ORDER_PROFILE.DELIVERY]: yup.string().required('–£–∫–∞–∂–∏—Ç–µ —Å–ø–æ—Å–æ–± –¥–æ—Å—Ç–∞–≤–∫–∏'),
    [ADD_ORDER_PROFILE.PAYMENT]: yup.string().required('–£–∫–∞–∂–∏—Ç–µ —Å–ø–æ—Å–æ–± –æ–ø–ª–∞—Ç—ã'),
});
